import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

export default function Home(props) {
  const [showSleepFormState, updateShowSleepFormState] = useState(false);
  const [showMealsFormState, updateShowMealsFormState] = useState(false);

  const [sleepState, updateSleepState] = useState();
  const [mealsState, updateMealsState] = useState();

  useEffect(() => {
    if (props.day) {
      updateSleepState(props.day.sleep);
      updateMealsState(props.day.meals.map((m) => m.name));
    }
  }, []);
  let sleepform = showSleepFormState ? (
    <form
      onSubmit={(e) => {
        updateShowSleepFormState(false);
        updateSleepState(
          e.target.hours.value * 60 * 60 * 1000 +
            e.target.minutes.value * 60 * 1000
        );
        props.submitSleepDuration(e);
      }}
    >
      <label htmlFor="hours">Hours</label>
      <input type="number" name="hours" id="hours"></input>
      <label htmlFor="minutes">Minutes</label>
      <input type="number" name="minutes" id="minutes"></input>
      <button type="submit">Submit</button>
    </form>
  ) : null;

  let mealsform = showMealsFormState ? (
    <form
      onSubmit={(e) => {
        updateShowMealsFormState(false);
        let newMeals = Array.from(
          e.target.meals.selectedOptions,
          (option) => option.innerText
        );
        updateMealsState(newMeals);
        props.submitMeals(e);
      }}
    >
      <label htmlFor="meals">Meals</label>
      <select name="meals" id="meals" multiple>
        {props.meals.map((meal) => {
          return (
            <option key={meal.name} value={meal._id}>
              {meal.name}
            </option>
          );
        })}
      </select>
      <button type="submit">Submit</button>
      <Link to="/newmeal">Create a new meal</Link>
    </form>
  ) : null;

  return (
    <>
      <h1>AD Manager Main</h1>
      {!props.user ? (
        <>
          <p>Please start by signing up or signing in!</p>
          <Link to="signup">Sign Up</Link>
          <Link to="signin">Sign In</Link>
        </>
      ) : (
        <>
          <Link to="meals">Favourite Meals</Link>
          <Link to="fast">Fasts</Link>
          <Link to="sleep">Sleeps</Link>
          <button onClick={props.onSignOut}>Sign Out</button>
          <div id="day">
            <p>
              Today is {new Date().getDate()}/{new Date().getMonth() + 1}/
              {new Date().getFullYear()}
            </p>
            {!props.day ? (
              <>
                <button onClick={props.onStart}>Start Your Day!</button>
              </>
            ) : !sleepState ? (
              <>
                <button
                  onClick={() => {
                    updateShowSleepFormState(true);
                  }}
                >
                  Submit last nights sleep
                </button>
                {sleepform}
              </>
            ) : (
              <>
                <p>
                  You got {(sleepState / 3600000).toFixed(2)}hrs of Sleep last
                  night
                </p>
                <button
                  onClick={() => {
                    updateShowSleepFormState(true);
                  }}
                >
                  Edit
                </button>
                {sleepform}
              </>
            )}
            {!mealsState ? (
              <>
                <button
                  onClick={() => {
                    updateShowMealsFormState(true);
                  }}
                >
                  What did you eat today?
                </button>
                {mealsform}
              </>
            ) : (
              <>
                <p>Today, you had</p>
                <ul>
                  {mealsState.map((meal) => (
                    <li>{meal}</li>
                  ))}
                </ul>
                <button
                  onClick={() => {
                    updateShowMealsFormState(true);
                  }}
                >
                  Edit
                </button>
                {mealsform}
              </>
            )}
          </div>
        </>
      )}
    </>
  );
}
